.page__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  text-align: center;
  @media screen and (max-width: $break-medium) {
    display: block;
  }

  .info__title {
    margin: auto;
  }
}

.info {
  main {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
}

@media screen and (max-width: $break-xl) {
  .info {
    .column {
      margin-right: auto;
      padding-right: 0;
    }
    .info__section {
      .info__sectionTitle {
        text-align: center;
        margin-right: 2rem;
      }
    }
  }
}

.info__cover {
  width: 40rem;
  flex-basis: 40rem;
  max-width: 100%;

  @media screen and (min-width: $break-xl) {
    & + .info__section {
      flex-basis: calc(100% - 50rem);

      .column {
        margin-left: auto;
        margin-right: 0;
      }
    }
  }
  @media screen and (max-width: $break-medium) {
    margin: 0 auto;
  }
}

.info__section {
  padding: 2rem 0;
  flex-basis: 100%;

  /* a {
		color: $seafoam;
		text-decoration: underline;
	} */
  ul {
    column-count: 2;
  }

  li {
    list-style: disc outside;
    margin-left: 1.2em;
  }
}

@media screen and (max-width: $break-small) {
  .info__section ul {
    column-count: initial;
  }
}

.info__section--standard {
  color: $pink;

  .info__sectionTitle {
    border-bottom: 2px solid currentColor;
    text-align: left;
    margin-left: 2rem;
  }
}

.info__title,
.info__sectionTitle {
  font-family: $serif;
  text-align: center;
  font-weight: 900;
}

.info__title {
  font-size: $h2;
  margin: 1em auto;
}

.info__sectionTitle {
  display: block;
  margin: 0 auto 3rem;
  padding-top: 2rem;
  text-transform: none;
}

.info__subtitle {
  margin-top: 1rem;
}

.info__separator {
  text-align: center;
  color: $grey3;
  position: relative;
  margin: 8rem auto 2rem;

  &:after,
  &:before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    position: relative;
    width: 14rem;
    margin: 0 1rem;
    height: 2px;
    background: $grey1;
  }
}

.info__section:nth-of-type(3n + 1) {
  .highlight svg {
    fill: $pink-medium-light;
  }
}

.info__section:nth-of-type(3n + 2) {
  .highlight svg {
    fill: $seafoam-light;
  }
}

.info__section:nth-of-type(3n + 3) {
  .highlight svg {
    fill: $orange-light;
  }
}

.info__block {
  margin-top: 2rem;
}

.info__block__header {
  margin: 1rem 0;
  text-transform: none;
  position: relative;
  text-decoration: none;

  /*&:after {
		content: '';
		position: absolute;
		top: 100%;
		left: -2rem;
		width: calc(100% + 4rem);
		height: 1px;
		background-color: currentColor;
	}*/
}

.info__section__cta {
  text-align: center;
}

.info__section--standard .info__section__cta {
  margin-top: 2rem;
  text-align: left;

  .cta {
    padding: 0;
    border: none;
    text-decoration: underline;
    color: #00ff81;
    &:before,
    &:after {
      display: none;
    }
  }
}

.info__section--contact {
  text-align: center;

  .info__blocks {
    display: flex;
    flex-wrap: wrap;
  }

  .info__block {
    width: 50%;
    padding: 0 2rem;
    margin-bottom: 2rem;
    margin-top: 0;

    .info__cover {
      width: 80%;
    }
  }
}

.info__section--standard {
  .info__sectionTitle {
    text-transform: initial;
  }

  .highlight svg {
    display: none;
  }
}

.info__buttons {
  margin-top: 3rem;
}

.info--love-club .info__section--pay-what-you-can {
  text-align: center;
}

@media screen and (max-width: $break-large) {
  .info .info__section--standard .info__sectionTitle {
    margin-left: auto;
    margin-right: auto;
    max-width: 40rem;
  }
}
